<template>
  <div class="grid min-h-screen place-items-center">
    <div class="container p-3 bg-gray-100 rounded md:p-5">
      <div class="flex items-center justify-center">
        <img
          src="https://img.icons8.com/external-inipagistudio-mixed-inipagistudio/64/000000/external-routine-mental-wellbeing-inipagistudio-mixed-inipagistudio.png"
        />
      </div>
      <h2 class="my-3 text-2xl font-semibold text-center">Select Your Plan</h2>
      <div>
        <vee-form :validation-schema="schema" @submit="submit">
          <!-- Name -->
          <div class="mb-3">
            <label class="inline-block mb-2 dark:text-white"
              >Starting Date</label
            >
            <vee-field
              v-model="start"
              @input="calculateDays"
              type="date"
              name="start"
              class="
                block
                w-full
                py-1.5
                px-3
                text-gray-800
                border border-gray-300
                transition
                duration-500
                focus:outline-none
                focus:border-black
                rounded
              "
              placeholder="When do you want to start?"
            />
            <ErrorMessage class="text-red-600 dark:text-red-500" name="start" />
          </div>
          <div class="mb-3">
            <label class="inline-block mb-2 dark:text-white">Ending Date</label>
            <vee-field
              v-model="end"
              @input="calculateDays"
              type="date"
              name="end"
              class="
                block
                w-full
                py-1.5
                px-3
                text-gray-800
                border border-gray-300
                transition
                duration-500
                focus:outline-none
                focus:border-black
                rounded
              "
              placeholder="How many days do u want to spend?"
            />
            <ErrorMessage class="text-red-600 dark:text-red-500" name="end" />
          </div>
          <div class="mb-3">
            <label class="inline-block mb-2 dark:text-white">Total Days</label>
            <input
              type="text"
              v-model="days"
              readonly
              class="
                block
                w-full
                py-1.5
                px-3
                text-gray-800
                border border-gray-300
                transition
                duration-500
                focus:outline-none
                focus:border-black
                rounded
              "
            />
          </div>
          <div class="text-center">
            <button
              class="px-6 py-3 font-semibold text-white transition duration-200 shadow  hover:shadow-lg bg-gradient-to-tl from-indigo-500 hover:from-blue-700 to-indigo-800"
            >
              Next
            </button>
          </div>
        </vee-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      schema: {
        start: "required",
        end: "required",
      },
      days: 0,
      start: null,
      end: null,
    };
  },
  methods: {
    submit() {
      if (this.days > 0) {
        this.$store.dispatch("setOptions", {
          options: { days: this.days, start: this.start },
          router: this.$router,
        });
      }
    },
    calculateDays() {
      this.days =
        (new Date(this.end).getTime() - new Date(this.start).getTime()) /
        (1000 * 3600 * 24);
    },
  },
};
</script>

